<template>
  <div class="container-body">
    <h1> </h1>
    <div>
      <div class="textForm">
        <input name="hospital-name" type="text" class="input-field" placeholder="" v-model="hospitalName">
      </div>
      <div class="textForm">
        <input name="hospital-phone" type="text" class="input-field" placeholder=" " v-model="hospitalPhone">
      </div>
      <span v-show="!phoneValid && hospitalPhone" class="input-error">
            .
      </span>
      <div class="textForm">
        <input name="hospital-address" type="text" class="input-field" placeholder=" " @click="execDaumPostcode()"
          v-model="hospitalAddress">
      </div>
      <div class="textForm">
        <input name="hospital-zipcode" type="text" class="input-field" placeholder=" " @click="execDaumPostcode()"
          v-model="hospitalZipcode">
      </div>
      <div class="textForm">
        <input name="hospital-description" type="text" class="input-field" placeholder="  " maxlength="254"
          v-model="hospitalDescription">
      </div>
      <div class="textForm">
        <input name="license-number" type="text" class="input-field" placeholder="  " v-model="licenseNumber">
      </div>

      <hr/>
      <div class="container-upload">
        <h3 class="text-center">  </h3>
        <input @change="upload" multiple type="file" accept="image/*">
      </div>

      <hr />
      <h3 class="text-center"> </h3>
      <small> 1   .</small><br /><br />
      <div>
        <div v-for="(department, idx) in $store.state.departments" :key="idx" class="form-check form-check-inline">
          <input class="form-check-input" type="checkbox" v-model="this.departments" :value="department">
          <label class="form-check-label" for="inlineCheckbox1">{{ department.departmentName }}</label>
        </div>
      </div>
      <hr />


      <h3 class="text-center">  </h3>
      <small>30   .<br/> 1 .</small><br /><br />
      <div id="opening-hours-select" class="col-md-3">
        <div id="selected-input" class="mb-3">
          <div class="select">
            <select class="form-select" aria-label="Default select example" v-model="selectedDay">
              <option selected> </option>
              <option v-for="(day,idx) in dayOfWeeks" :value="day" :key="idx">{{day}}</option>
            </select>
          </div>
          <div class="select">
            <select class="form-select" aria-label="Default select example" v-model="selectedStartTime">
              <option selected> </option>
              <option v-for="(hour,idx) in hours" :value="hour" :key="idx">{{hour}}</option>
            </select>
          </div>
          <div class="select">
            <select class="form-select" aria-label="Default select example" v-model="selectedEndTime">
              <option selected> </option>
              <option v-for="(hour,idx) in hours" :value="hour" :key="idx">{{hour}}</option>
            </select>
          </div>
          <div class="select">
            <select class="form-select" aria-label="Default select example" v-model="selectedLunchTime">
              <option selected> </option>
              <option v-for="(hour,idx) in hours" :value="hour" :key="idx">{{hour}}</option>
            </select>
          </div>          
          <button class="btn btn-primary" @click="selectOpeningHours();"></button>
        </div>
        <div id="selected-data">
          <ul class="list-group">
            <li class="list-group-item hour-item" v-for="(item, idx) in openingHours" :key="idx">
              <div class="item">{{dayOfWeekConvert(item.dayOfTheWeek)}}</div>
              <div class="item"> : {{ item.open }} ~ {{ item.close }}</div>
              <div class="item"> : {{ item.lunchHour }}</div>
              <button type="button" class="btn-close" id="closeBtn" @click="deleteOpeningHours(item.dayOfTheWeek);"></button>
            </li>
          </ul>
        </div>
      </div>
      <hr />

      <button class="btn btn-primary registerDoctor" type="submit" @click="submitForm()"> </button>
      <div>

      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  data() {
    return {
      hospitalName: '',
      hospitalPhone: '',
      hospitalZipcode: '',
      hospitalAddress: '',
      hospitalDescription: '',
      departments: [],
      licenseNumber: '',
      photoUrl: '',
      openingHours: [], //  { dayOfTheWeek: 'Mon', open: '5:00', close: '19:00', lunchHour: '12:00'}
      selectedDay : ' ',
      selectedStartTime: ' ',
      selectedEndTime: ' ',
      selectedLunchTime: ' ',
      phoneValid: false,
      phoneHasError: false,
      dayOfWeeks: ['', '', '', '', '', '', ''],
      isDayOfWeeksSelected: {'' : false, '' : false, '' : false, '' : false, '' : false, '' : false, '' : false},
      hours: [
        '0:00','0:30','1:00','1:30','2:00','2:30','3:00','3:30','4:00','4:30',
        '5:00','5:30','6:00','6:30','7:00','7:30','8:00','8:30','9:00','9:30',
        '10:00','10:30', '11:00', '11:30', '12:00','12:30','13:00', '13:30',
        '14:00','14:30', '15:00', '15:30', '16:00', '16:30', '17:00', '17:30',
        '18:00','18:30','19:00','19:30','20:00','20:30','21:00','21:30','22:00',
        '22:30','23:00','23:30','24:00',
      ],
    }
  },
  computed: {
    ...mapState(['currentUser']),
  },
  methods: {
    submitForm() {
      if (!this.emptyColoumn() || !this.phoneValid) {
        alert("  .    .")
        return;
      }
      if (confirm("        .\n  ?")) {
        this.$store.dispatch('doctorRegist', {
          doctorId: this.currentUser.userId,
          hospitalName: this.hospitalName,
          hospitalPhone: this.hospitalPhone,
          hospitalZipcode: this.hospitalZipcode,
          hospitalAddress: this.hospitalAddress,
          hospitalDescription: this.hospitalDescription,
          departments: this.departments,
          licenseNumber: this.licenseNumber,
          photoUrl: 'https://meet-doctor.s3.ap-northeast-2.amazonaws.com/'+this.photoUrl[0]
        }).then(() => {
          this.$store.dispatch('setOpeningHour', {
            doctorId: this.currentUser.userId,
            openingHours: this.openingHours,
          })
        })
      }
    },
    emptyColoumn() {
      if (this.hospitalName && this.hospitalPhone && this.hospitalZipcode &&
        this.hospitalAddress && this.hospitalDescription && this.licenseNumber && this.departments.length > 0) return true;
      return false;
    },
    execDaumPostcode() {
      new window.daum.Postcode({
        oncomplete: (data) => {
          if (this.extraAddress !== "") {
            this.extraAddress = "";
          }
          if (data.userSelectedType === "R") {
            //     
            this.hospitalAddress = data.roadAddress;
          } else {
            //     (J)
            this.hospitalAddress = data.jibunAddress;
          }

          //       .
          if (data.userSelectedType === "R") {
            //    . ( )
            //     "//" .
            if (data.bname !== "" && /[||]$/g.test(data.bname)) {
              this.extraAddress += data.bname;
            }
            //  ,   .
            if (data.buildingName !== "" && data.apartment === "Y") {
              this.extraAddress +=
                this.extraAddress !== ""
                  ? `, ${data.buildingName}`
                  : data.buildingName;
            }
            //    ,     .
            if (this.extraAddress !== "") {
              this.extraAddress = `(${this.extraAddress})`;
            }
          } else {
            this.extraAddress = "";
          }
          //  .
          this.hospitalZipcode = data.zonecode;
        },
      }).open();
    },
    checkPhone() {
      const validatePhoneNumber = /^\d{2,3}-\d{3,4}-\d{4}$/;

      if (!validatePhoneNumber.test(this.hospitalPhone) || !this.hospitalPhone) {
        this.phoneValid = false;
        this.phoneHasError = true;
      }
      else {
        this.phoneValid = true;
        this.phoneHasError = false;
      }
    },    
		selectOpeningHours() {
      // 
      if((!this.selectedDay || this.selectedDay != ' ') && (!this.selectedStartTime || this.selectedStartTime != ' ')
      && (!this.selectedEndTime || this.selectedEndTime  != ' ') && (!this.selectedLunchTime || this.selectedLunchTime != ' ')) {
        //pass
      } else {
        alert("  .")
        return
      }
      //  
      if(this.isDayOfWeeksSelected[this.selectedDay]) {
        alert("  .")
        return
      }
      //open close   
      if(this.changeTimeToInt(this.selectedStartTime) >= this.changeTimeToInt(this.selectedEndTime)) {
        alert("     .")
        return
      }
      //  
      if(this.changeTimeToInt(this.selectedStartTime) > this.changeTimeToInt(this.selectedLunchTime) || this.changeTimeToInt(this.selectedEndTime) <= this.changeTimeToInt(this.selectedLunchTime)) {
        alert("   .")
        return
      }
      this.openingHours.push({ "dayOfTheWeek" : this.dayOfWeekConvert(this.selectedDay), "open" : this.selectedStartTime, "close" : this.selectedEndTime, "lunchHour" : this.selectedLunchTime });
      this.isDayOfWeeksSelected[this.selectedDay] = true;
		},
    dayOfWeekConvert(str) {
      if(str == '') return 'Mon'
      else if(str == '') return 'Tue'
      else if(str == '') return 'Wed'
      else if(str == '') return 'Thu'
      else if(str == '') return 'Fri'
      else if(str == '') return 'Sat'
      else if(str == '') return 'Sun'
      else if(str == 'Mon') return ''
      else if(str == 'Tue') return ''
      else if(str == 'Wed') return ''
      else if(str == 'Thu') return ''
      else if(str == 'Fri') return ''
      else if(str == 'Sat') return ''
      else if(str == 'Sun') return ''
    },    
		changeTimeToInt(target) {      
      return parseInt(target.replace(':',''));
		},
    deleteOpeningHours(target) {
      for(let i = 0; i < this.openingHours.length; i++){
        if(this.openingHours[i].dayOfTheWeek === target) {
          this.openingHours.splice(i,1);
          this.isDayOfWeeksSelected[this.dayOfWeekConvert(target)+""] = false;
          return;
        }
      }
    },
    upload(e){
      const formData = new FormData();
      var files = e.target.files;
      const maxSize = 10* 1024* 1024;//  10MB
      var fileSize = 0;
      var type = '';
      //    
      for (let i = 0; i < files.length; i++) {
        type = files[i].type.toLowerCase();
        fileSize += files[i].size;
        if(type === 'image/png' || type === 'image/jpg' || type === 'image/jpeg' || type === 'image/gif'){
          formData.append("images", files[i]);
        }else{
          alert('  jpg, jpeg, png, gif  !');
          e.target.value = '';//  
          return;   
        }
      }
      // 
      if(fileSize > maxSize){
        alert('  10MB  !');
        return;
      }
      if(e.target.value !== ''){
        this.$store.dispatch('upload', formData).then((res) => {
          console.log(res.data);
          this.photoUrl = res.data;
          // this.setphooUrl(res.data);
        });
      }
    }
  },
  created() {
    let userId = localStorage.getItem('userId');
    this.$store.dispatch('getCurrentUserInfo', userId);
    this.$store.dispatch('getDepartments');
  },
  watch: {
    'hospitalPhone': function () {
      this.checkPhone()
    }
  }
}
</script>

<style>
.registerDoctor {
  margin-top: 20px;
}

* {
  margin: 0px;
  padding: 0px;
  text-decoration: none;
  font-family: sans-serif;

}

body {
  background-image: #34495e;
}

.joinForm {
  position: absolute;
  width: 400px;
  height: 400px;
  padding: 30px, 20px;
  background-color: #FFFFFF;
  text-align: center;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 15px;
}

.textForm {
  border-bottom: 2px solid #adadad;
  margin: 30px;
  padding: 10px 10px;
}

.input-field {
  width: 100%;
  border: none;
  outline: none;
  color: #636e72;
  font-size: 16px;
  height: 25px;
  background: none;
}

.text-center {
  text-align: center;
}

#opening-hours-select {
  width: 100%;
}

#selected-input {
  display: flex;
}

.select {
  margin: auto auto auto 0;
}

.hour-item {
  display: flex;
  width: 100%;
}

.item {
  margin: auto;
}

#selected-data {
  white-space: pre;
  overflow-y: auto;
  height: 300px;
  border: 0.5px solid rgb(223, 223, 223);
}
</style>